import React, { forwardRef, useCallback, useMemo } from 'react';
import T from 'prop-types';
import styled from 'styled-components';
import { SortableContainer } from 'react-sortable-hoc';
import { reset } from '@splunk/themes/mixins';
import SubmitButton from '@splunk/dashboard-ui/SubmitButton';

import { customThemeVariables } from '@splunk/dashboard-ui/customThemeVariables';

const InputContainer = styled.div`
    ${reset('block')};
    padding: 8px 0px;
    background: ${(prop) =>
        prop.backgroundColor || customThemeVariables.dashboardBackgroundColor};
`;

const SortableInputContainer = SortableContainer(InputContainer);
// give it a display name so that Enzyme can find it https://enzymejs.github.io/enzyme/docs/api/selector.html
SortableInputContainer.displayName = 'SortableInputContainer';

/**
 * Simple Input Layout
 * @param {*} param0
 */
const InputLayout = forwardRef(
    (
        {
            submitButton,
            submitButtonDisabled,
            inputStructure,
            dashboardCoreApi,
            renderInput,
            onSubmitButtonClick,
        },
        ref
    ) => {
        const onSortEnd = useCallback(
            ({ oldIndex, newIndex }) => {
                const newInputStructure = [...inputStructure];
                const [inputId] = newInputStructure.splice(oldIndex, 1);
                newInputStructure.splice(newIndex, 0, inputId);

                dashboardCoreApi.updateInputStructure(newInputStructure);
            },
            [dashboardCoreApi, inputStructure]
        );

        const submit = useMemo(
            () =>
                inputStructure.length && submitButton ? (
                    <SubmitButton
                        data-test="input-item-submit-button"
                        key="submit_button"
                        disabled={submitButtonDisabled}
                        onClick={onSubmitButtonClick}
                    />
                ) : null,
            [
                inputStructure.length,
                submitButton,
                submitButtonDisabled,
                onSubmitButtonClick,
            ]
        );

        if (inputStructure.length === 0) {
            return null;
        }

        return (
            <div ref={ref}>
                <SortableInputContainer
                    axis="xy"
                    useDragHandle
                    onSortEnd={onSortEnd}
                    data-test="input-layout-container"
                >
                    {inputStructure.map((inputId, index) =>
                        renderInput({ inputId, index })
                    )}
                    {submit}
                </SortableInputContainer>
            </div>
        );
    }
);

InputLayout.propTypes = {
    /**
     * whether to disable the submit button
     */
    submitButtonDisabled: T.bool,
    /**
     * whether to display submit button
     */
    submitButton: T.bool,
    /**
     * input id array
     */
    inputStructure: T.arrayOf(T.string),
    /**
     * dashboardCoreApi
     */
    dashboardCoreApi: T.object.isRequired,
    /**
     * input component instances
     */
    renderInput: T.func,
    /**
     * callback when user clicks submit button
     */
    onSubmitButtonClick: T.func,
};

InputLayout.defaultProps = {
    submitButtonDisabled: false,
    submitButton: false,
    inputStructure: [],
    onSubmitButtonClick: () => {},
    renderInput: () => null,
};

export default InputLayout;
